<template>
  <v-card id="SymptomsDialogCard" tile>
    <v-card-title class="light-green headline white--text">
      <v-icon large color="white" class="mr-2">notes</v-icon>
      <span>Περιγραφή Συμπτωμάτων</span>
    </v-card-title>
    <v-card-text class="pa-0">
      <v-list class="pa-0 transparent" three-line>
        <template v-for="(symptom, s) in Object.keys(description)">
          <v-subheader class="light-green lighten-4" :key="`subheader-${s}`">
            <span>{{ description[symptom].name }} αποτελεί</span>
            <v-icon small>keyboard_arrow_right</v-icon>
          </v-subheader>
          <v-divider :key="`topDivider-${s}`"></v-divider>
          <v-list-tile class="py-1 " :key="`tile-${s}`">
            <v-list-tile-avatar>
              <v-icon medium color="light-green">visibility</v-icon>
            </v-list-tile-avatar>
            <v-list-tile-content>
              <span>{{ description[symptom].description }}</span>
            </v-list-tile-content>
          </v-list-tile>
        </template>
      </v-list>
    </v-card-text>
    <v-divider></v-divider>
    <v-card-actions class="justify-center">
      <slot></slot>
    </v-card-actions>
  </v-card>
</template>

<script>
export default {
  name: 'SymptomsDialogCard',
  props: ['plant', 'description'],
  data () {
    return {
    }
  }
}
</script>

<style lang="css">
</style>



