<template>
  <v-container fluid grid-list-lg class="pa-0">
    <v-layout row wrap justify-center>
      <v-flex v-for="(plant, i) in plants" :key="i" sm12 md6 lg4>
        <v-card tile hover>
          <progressive-img
            :src="plant.eng ? `/static/plants/${plant.eng}.jpg` : '/static/unknownPlant.jpg'"
            placeholder="/static/placeholder.png"
            :blur="3.4"
            :aspect-ratio="0.6">
          </progressive-img>
          <v-card-title class="pa-0">
            <v-flex class="pa-0">
              <v-list style="width:100%;" class="pa-0" two-line>
                <v-list-tile
                  :href="wikipedia(plant.gr)"
                  :class="plant.vue_color || 'secondary'"
                  class="white--text"
                  target="_blank">
                  <v-list-tile-avatar>
                    <v-icon color="white" medium>info</v-icon>
                  </v-list-tile-avatar>
                  <v-list-tile-content>
                    <v-list-tile-title>{{ plant.gr }}</v-list-tile-title>
                    <v-list-tile-sub-title class="white--text">
                      Κοινή Ονομασία
                    </v-list-tile-sub-title>
                  </v-list-tile-content>
                  <v-list-tile-action><v-icon color="white" medium>link</v-icon></v-list-tile-action>
                </v-list-tile>
                <v-list-tile>
                  <v-list-tile-avatar>
                    <v-icon :color="plant.vue_color" medium>school</v-icon>
                  </v-list-tile-avatar>
                  <v-list-tile-content>
                    <v-list-tile-title><em>{{ plant.latin }}</em></v-list-tile-title>
                    <v-list-tile-sub-title>Επιστημονική Ονομασία</v-list-tile-sub-title>
                  </v-list-tile-content>
                </v-list-tile>
                <v-divider :class="plant.vue_color + ' lighten-4'" inset></v-divider>
                <v-list-tile :to="`/edit/${plant.gr}`">
                  <v-list-tile-avatar>
                    <v-icon :color="plant.vue_color" medium>edit</v-icon>
                  </v-list-tile-avatar>
                  <v-list-tile-content>
                    <v-list-tile-title>Επεξεργασία</v-list-tile-title>
                    <v-list-tile-sub-title>Επεξεργασία κηπευτικού</v-list-tile-sub-title>
                  </v-list-tile-content>
                  <v-list-tile-action><v-icon medium>keyboard_arrow_right</v-icon></v-list-tile-action>
                </v-list-tile>
                <v-divider :class="plant.vue_color + ' lighten-4'" inset></v-divider>
                <v-list-tile :to="`/diagnosis/${plant.gr}`" :disabled="plant.diseases.length == 0">
                  <v-list-tile-avatar>
                    <v-icon :color="plant.vue_color" medium>assignment</v-icon>
                  </v-list-tile-avatar>
                  <v-list-tile-content>
                    <v-list-tile-title>Διάγνωση</v-list-tile-title>
                    <v-list-tile-sub-title>Διαγνωστικός οδηγός</v-list-tile-sub-title>
                  </v-list-tile-content>
                  <v-list-tile-action><v-icon medium>keyboard_arrow_right</v-icon></v-list-tile-action>
                </v-list-tile>
                <v-divider :class="plant.vue_color + ' lighten-4'" inset></v-divider>
                <v-list-tile @click="showDiseaseList(plant)" :disabled="plant.diseases.length == 0">
                  <v-list-tile-avatar>
                    <v-icon :color="plant.vue_color" medium>local_hospital</v-icon>
                  </v-list-tile-avatar>
                  <v-list-tile-content>
                    <v-list-tile-title>Ασθένειες</v-list-tile-title>
                    <v-list-tile-sub-title>
                      {{ plant.diseases.length > 0 ? `${plant.diseases.length} στο αρχείο` : 'Καμία'}}
                    </v-list-tile-sub-title>
                  </v-list-tile-content>
                  <v-list-tile-action>
                    <v-icon medium>{{ plant.diseaseExpand ? 'expand_less' : 'expand_more' }}</v-icon>
                  </v-list-tile-action>
                </v-list-tile>
              </v-list>
            </v-flex>
          </v-card-title>
          <v-slide-y-transition>
            <catalog-disease-list :plant="plant" v-if="plant.diseaseExpand"></catalog-disease-list>
          </v-slide-y-transition>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import CatalogDiseaseList from './CatalogDiseaseList'
import { bus } from '@/lib/bus'
export default {
  name: 'PlantsCatalog',
  props: ['plants'],
  components: {
    'catalog-disease-list': CatalogDiseaseList
  },
  data () {
    return {
    }
  },
  computed: {
    wikipedia: function () {
      return plant => `https://el.wikipedia.org/wiki/${plant}`
    }
  },
  methods: {
    showDiseaseList (plant) {
      plant.diseaseExpand = (plant.diseases.length === 0) ? false : !plant.diseaseExpand
    },
    printArray (array) {
      return array.join(', ').toString()
    },
    selectPlant (plant) {
      bus.$emit('choosed-plant', plant)
      this.$router.push(`/diagnosis/${plant}`)
    }
  }
}
</script>

<style lang="css" scoped>
.ntt{
  text-transform: none;
}
</style>



