<template>
  <CardLayout icon="local_hospital" title="Σελίδα Ασθένειας" id="Diseases">
    <template slot="content">
      <v-slide-x-transition>
        <v-card hover key="card" v-if="plant !== null" tile class="my-4">
          <v-card-title :class="plant.vue_color || 'secondary'" class="title white--text pa-0">
            <v-avatar tile color="white" size="55" class="ml-0 mr-2">
              <v-icon medium :color="plant.vue_color" class="mx-2">local_hospital</v-icon>
            </v-avatar>
            <span v-if="diseaseNotExist">{{ plant.gr }}</span>
            <template v-else>
              <span class="white--text">{{ plant.gr }}</span>
              <v-icon :color="plant.vue_color + ' lighten-4'">keyboard_arrow_right</v-icon>
              <span class="white--text">{{ disease.name }}</span>
            </template>
          </v-card-title>
          <v-divider></v-divider>
          <v-card-text class="pa-0">
            <v-list three-line v-if="!diseaseNotExist" class="pa-0">
              <v-list-tile>
                <v-list-tile-avatar>
                  <v-icon medium :color="plant.vue_color">title</v-icon>
                </v-list-tile-avatar>
                <v-list-tile-content>
                  <v-list-tile-title>
                    {{ disease.parasitic ? 'Παρασιτική':'Μη παρασιτική' }}
                  </v-list-tile-title>
                  <v-list-tile-sub-title>Τύπος ασθένειας</v-list-tile-sub-title>
                </v-list-tile-content>
              </v-list-tile>
              <v-divider inset></v-divider>
              <template v-for="(info, i) in diseaseInfo">
                <v-list-tile :key="`tile-${i}`">
                  <v-list-tile-avatar>
                    <v-icon medium :color="plant.vue_color">{{ info.icon }}</v-icon>
                  </v-list-tile-avatar>
                  <v-list-tile-content>
                    <em v-if="disease.parasitic && info.isPathogenTile">{{ disease.pathogen }}</em>
                    <span v-else>{{ disease[info.key].toString() }}</span>
                    <v-list-tile-sub-title>{{ info.sub }}</v-list-tile-sub-title>
                  </v-list-tile-content>
                </v-list-tile>
                <v-divider inset :key="`divider-${i}`"></v-divider>
              </template>
              <v-list-tile>
                <v-list-tile-avatar>
                  <v-icon medium :color="plant.vue_color">visibility</v-icon>
                </v-list-tile-avatar>
                <v-list-tile-content>
                  <v-list-tile-title>
                    Εκδηλώνεται με {{Object.keys(disease.rules).join(', ')}}
                  </v-list-tile-title>
                  <v-list-tile-sub-title>Συμπτωματολογία στο φυτό {{ plant.gr }}</v-list-tile-sub-title>
                </v-list-tile-content>
              </v-list-tile>
              <v-divider inset></v-divider>
              <v-list-tile @click="showTreatment = !showTreatment">
                <v-list-tile-avatar>
                  <v-icon medium :color="plant.vue_color">security</v-icon>
                </v-list-tile-avatar>
                <v-list-tile-content>
                  <v-list-tile-title>
                    {{ disease.treatment.length }}
                    {{ disease.treatment.length == 1 ? 'μέτρο':'μέτρα' }}
                    αντιμετώπισης
                  </v-list-tile-title>
                  <v-list-tile-sub-title>Καταπολέμηση</v-list-tile-sub-title>
                </v-list-tile-content>
                <v-list-tile-action>
                  <v-icon>{{ showTreatment ? 'expand_less':'expand_more' }}</v-icon>
                </v-list-tile-action>
              </v-list-tile>
              <v-slide-y-transition>
                <div v-if="showTreatment">
                  <v-list class="pa-0">
                    <template v-for="(treatment, t) in disease.treatment">
                      <v-list-tile :key="t" :class="plant.vue_color + ' lighten-5'">
                        <v-list-tile-avatar><v-icon>security</v-icon></v-list-tile-avatar>
                        <v-list-tile-content>
                          <v-list-tile-title>{{ treatment }}.</v-list-tile-title>
                          <v-list-tile-sub-title>Νο.{{ t+1 }} μέτρο</v-list-tile-sub-title>
                        </v-list-tile-content>
                      </v-list-tile>
                      <v-divider class="grey" v-if="t+1 < disease.treatment.length" :key="`divider-${t}`">
                      </v-divider>
                    </template>
                  </v-list>
                </div>
              </v-slide-y-transition>
            </v-list>
            <div v-if="diseaseNotExist">
              <v-alert
                type="error"
                transition="slide-x-transition"
                :value="true"
                class="ma-0">
                Η ασθένεια <em><b>{{ $route.params.disease }}</b></em>, δεν βρέθηκε στο αρχείο του φυτού {{ plant.gr }}.
                <br>
                Για πλήρες αρχείο ασθενειών του κηπευτικού, μεταβείτε στη Σελίδα Διάγνωσης πατώντας στο κουμπί <v-icon color="white" class="pb-1">local_florist</v-icon>.
              </v-alert>
              <v-divider :class="plant.vue_color + ' lighten-2'"></v-divider>
              <v-btn class="ma-0" depressed block large color="white" @click="toDiagnosis">
                <v-icon large :color="plant.vue_color">local_florist</v-icon>
              </v-btn>
            </div>
          </v-card-text>
        </v-card>
      </v-slide-x-transition>
      <LoadingPlants class="my-4" key="loading" v-if="plant == null"></LoadingPlants>
    </template>
  </CardLayout>
</template>

<script>
import API from '@/lib/API'
import LoadingPlants from './LoadingPlants'
import CardLayout from '@/components/layouts/CardLayout'
import { bus } from '@/lib/bus'

export default {
  name: 'Diseases',
  components: {
    LoadingPlants,
    CardLayout
  },
  data () {
    return {
      plant: null,
      disease: null,
      selectedPlant: false,
      diseaseInfo: [{
        icon: 'info',
        key: 'name',
        sub: 'Ονομασία ασθένειας'
      }, {
        icon: 'bug_report',
        key: 'pathogen',
        isPathogenTile: true,
        sub: 'Παθογόνο αίτιο'
      }, {
        icon: 'description',
        key: 'description',
        sub: 'Σύντομη περιγραφή'
      }],
      showTreatment: false
    }
  },
  created () {
    API.getPlant(this.routePlant).then(plants => {
      this.plant = plants[0]
      this.disease = this.plant.diseases.filter(disease => disease.name === this.$route.params.disease)[0]
    })
  },
  mounted () {
    bus.$on('choosed-plant', selectedPlant => {
      this.selectedPlant = selectedPlant
    })
  },
  computed: {
    routePlant () {
      return this.$route.params.plant
    },
    plantNotExist () {
      return this.plant === null
    },
    diseaseNotExist () {
      return this.plant.diseases.filter(disease => disease.name === this.$route.params.disease).length === 0
    }
  },
  methods: {
    toDiagnosis () {
      this.$router.push('/diagnosis')
    }
  }
}
</script>

<style lang="css">
.nbr{
  border-radius: 0px;
}
.ntd{
  text-decoration: none;
}
</style>



