import _ from 'lodash'
export default {
  possibleDiseases (DISEASES, SELECTED_SYMPTOMS) {
    DISEASES = DISEASES.filter(disease => {
      var diseaseSymptoms = Object.keys(disease.rules)
      disease.possible = true // Disease possible index
      SELECTED_SYMPTOMS.forEach(selectedSymptom => {
        // Έστω κι ένα σύμπτωμα να μην υπάρχει η ασθένεια ΑΠΟΚΛΕΙΕΤΑΙ
        if (diseaseSymptoms.indexOf(selectedSymptom) === -1) disease.possible = false
      })
      return disease.possible // Return only possible diseases
    })
    return DISEASES // Return diseases for use directly from function
  },
  possiblePreview (DISEASES) {
    var ALERT = { value: true }
    if (DISEASES.length === 0) {
      ALERT.type = 'error'
      ALERT.color = 'red'
      ALERT.icon = 'not_interested'
      ALERT.message = 'Καμία πιθανή ασθένεια με το συνδυασμό.'
    } else if (DISEASES.length === 1) {
      ALERT.type = 'success'
      ALERT.color = 'green'
      ALERT.icon = 'local_hospital'
      ALERT.message = `Το κηπευτικό πάσχει από την ασθένεια ${DISEASES[0].name}.`
    } else {
      ALERT.type = 'info'
      ALERT.color = 'blue'
      ALERT.icon = 'assignment'
      ALERT.message = `${DISEASES.length} πιθανές ασθένειες με το συνδυασμό.`
    }
    return ALERT
  },
  steps (DISEASES, SYMPTOMS) {
    var stepArray = []
    DISEASES.forEach(disease => {
      SYMPTOMS.forEach(symptom => {
        _.forOwn(disease.rules[symptom], (values, qualifier) => {
          var step = {
            qualifier: qualifier,
            disabled: true,
            selectedValues: []
          }
          if (stepArray.filter(s => s.qualifier === qualifier).length === 0) {
            step.values = values
            stepArray.push(step)
          } else {
            stepArray.find(s => s.qualifier === qualifier).values =
            _.union(stepArray.find(s => s.qualifier === qualifier).values, values)
          }
        })
      })
    })
    return stepArray
  },
  symptoms (DISEASES) {
    var symptoms = []
    DISEASES.forEach(disease => {
      _.forOwn(disease.rules, (qualifiers, symptom) => symptoms.push(symptom))
    })
    return _.uniq(symptoms)
  }
}



