<template>
  <div id="selectSymptom" class="pa-0">
    <v-card class="my-4">
      <v-card-title class="title white--text pa-0" :class="plant.vue_color || 'secondary'">
        <v-avatar tile color="white" size="55" class="ml-0 mr-2">
          <v-icon medium :color="plant.vue_color" class="mx-2">local_florist</v-icon>
        </v-avatar>
        <span>{{ plant.gr }}</span>
      </v-card-title>
      <v-divider></v-divider>
      <v-card-text class="pa-0" v-if="plant.diseases.length > 0">
        <v-stepper class="ma-0" vertical v-model="diagnosisCursor">
          <v-stepper-step :complete="diagnosisCursor > 1" :step="1">
            <span class="subheading">
              Βασικό Κριτήριο<v-icon class="pb-1">keyboard_arrow_right</v-icon>Το φυτό παρουσιάζει:
            </span>
            <v-spacer></v-spacer>
          </v-stepper-step>
          <v-divider class="mb-2"></v-divider>
          <v-stepper-content :step="1">
            <v-layout>
              <span class="body-2">
                <v-tooltip top>
                  <v-btn slot="activator" icon flat class="mx-0 mb-2"><v-icon>help</v-icon></v-btn>
                  <span>Οι μη πιθανές επιλογές καταργούνται αυτόματα</span>
                </v-tooltip>
                Επιλέξτε συνδυασμό συμπτωμάτων:
              </span>
              <v-spacer></v-spacer>
              <v-scale-transition>
                <v-btn small fab v-if="Object.keys(description).length > 0" class="my-0"
                  :color="plant.vue_color || 'secondary'" @click.native.stop="descriptionDialog = true">
                  <v-icon color="white" medium>visibility</v-icon>
                </v-btn>
              </v-scale-transition>
              <v-dialog transition="dialog-bottom-transition" v-model="descriptionDialog"
                fullscreen>
                <SymptomsDialogCard :plant="plant" :description="description">
                  <v-btn fab class="my-2" @click="descriptionDialog = false"
                    color="light-green">
                    <v-icon medium color="white">done</v-icon>
                  </v-btn>
                </SymptomsDialogCard>
              </v-dialog>
            </v-layout>
            <transition-group name="scale-transition">
              <div v-for="(symptom, s) in symptoms" :key="`div-${s}`">
                <v-switch :key="s"
                  @change="chooseSymptom"
                  :color="plant.vue_color || 'secondary'"
                  :label="symptom"
                  :value="symptom"
                  class="mx-3"
                  v-model="selectedSymptoms"
                  :loading="!description[symptom]">
                </v-switch>
              </div>
            </transition-group>
            <v-slide-x-reverse-transition>
              <v-card class="pa-0 ma-1" v-if="selectedSymptoms.length > 0">
                <v-list class="pa-0 ma-0">
                  <v-subheader :class="possiblePreview.color + ' lighten-4'">Συμπτώματα</v-subheader>
                  <v-divider></v-divider>
                  <transition-group name="slide-x-transition">
                    <template v-for="(symptom, s) in selectedSymptoms">
                      <v-list-tile :class="possiblePreview.color + ' lighten-5'" :key="`tile-${s}`">
                        <v-list-tile-avatar><v-icon>visibility</v-icon></v-list-tile-avatar>
                        <v-list-tile-content>
                          <v-list-tile-title>{{ symptom }}</v-list-tile-title>
                        </v-list-tile-content>
                      </v-list-tile>
                      <v-divider v-if="s+1 < selectedSymptoms.length" :key="`divider-${s}`"></v-divider>
                    </template>
                  </transition-group>
                  <v-list-tile :class="possiblePreview.color + ' lighten-3'">
                    <v-list-tile-avatar>
                      <v-icon>{{ possiblePreview.icon }}</v-icon>
                    </v-list-tile-avatar>
                    <v-list-tile-content>
                      <v-list-tile-title>Πόρισμα</v-list-tile-title>
                      <v-list-tile-sub-title>{{ possiblePreview.message }}</v-list-tile-sub-title>
                    </v-list-tile-content>
                  </v-list-tile>
                </v-list>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn flat icon @click="selectedSymptoms = []"><v-icon>refresh</v-icon></v-btn>
                  <v-btn round class="white--text" :disabled="POSSIBLE_DISEASES.length == 0" :color="possiblePreview.color" @click="diagnose">
                    <span v-if="POSSIBLE_DISEASES.length == 1">{{ POSSIBLE_DISEASES[0].name }}</span>
                    <span v-else><v-icon>arrow_forward_ios</v-icon></span>
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-slide-x-reverse-transition>
          </v-stepper-content>
          <template v-for="(step, s) in steps">
            <v-stepper-step
              :complete="diagnosisCursor > s+2"
              :key="`stepper-step_${s}`"
              :step="s + 2">
              <div class="subheading">{{ step.qualifier }}:</div>
            </v-stepper-step>
            <v-stepper-content :key="`stepper-content_${s}`" :step="s+2">
              <v-checkbox v-for="(value, v) in step.values"
                @change="selectValue(step, value)"
                :color="plant.vue_color"
                :key="`switch-${s}_${v}`"
                :label="value"
                :value="value"
                v-model="STEP_VALUES_MODEL">
              </v-checkbox>
              <v-layout row wrap>
                <v-spacer></v-spacer>
                <v-btn flat icon @click="resetSystem"><v-icon>refresh</v-icon></v-btn>
                <v-btn
                  round
                  :disabled="step.disabled"
                  :color="plant.vue_color || 'secondary'"
                  class="white--text"
                  @click="toNextStep(step)">
                  <v-icon>arrow_forward_ios</v-icon>
                </v-btn>
              </v-layout>
            </v-stepper-content>
          </template>
        </v-stepper>
      </v-card-text>
      <v-card-text class="subheading pa-0" v-else>
        <v-alert type="error" :value="true" class="alert ma-0 pa-4 subheading">
          Καμία ασθένεια για το φυτό <b>{{ plant.gr }}</b> στη Βάση Γνώσεως. Μεταβείτε στη <router-link class="white--text" :to="`/edit/${plant.gr}`">Σελίδα επεξεργασίας</router-link> του κηπευτικού.
        </v-alert>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
import API from '@/lib/API'
// import _ from 'lodash'
import I_E from '@/lib/InferenceEngine'
import SymptomsDialogCard from './SymptomsDialogCard'
import swal from 'sweetalert2'

export default {
  name: 'selectSymptom',
  props: ['sickPlant'],
  components: {
    'SymptomsDialogCard': SymptomsDialogCard
  },
  data () {
    return {
      plant: this.sickPlant,
      selectedSymptoms: [],
      description: {},
      descriptionDialog: false,
      diagnosisCursor: 1,
      COMPLETED_STEPS: [],
      STEP_VALUES_MODEL: [],
      DIAGNOSIS_END: false
    }
  },
  created () {
    API.getLibrary().then(library => { this.description = library[0].symptoms })
  },
  computed: {
    symptoms () {
      return I_E.symptoms(this.POSSIBLE_DISEASES)
    },
    steps () {
      return I_E.steps(this.POSSIBLE_DISEASES, this.selectedSymptoms)
    },
    POSSIBLE_DISEASES () {
      return I_E.possibleDiseases(this.plant.diseases, this.selectedSymptoms)
    },
    possiblePreview () {
      return I_E.possiblePreview(this.POSSIBLE_DISEASES)
    }
  },
  mounted () {},
  methods: {
    chooseSymptom (symptom) {
      if (this.POSSIBLE_DISEASES.length <= 1) { this.steps.length = 0 }
      console.log(this.steps)
    },
    diagnose () {
      var p = this.POSSIBLE_DISEASES
      switch (p.length) {
        case 0:swal({
          type: 'error',
          title: 'Καμία ασθένεια',
          html: `Καμία πιθανή ασθένεια στο αρχείο με <b>${this.selectedSymptoms.join(', ')}</b>. Αλλάξτε συνδυασμό και προσπαθήστε ξανά!`
        }); break
        case 1: this.$router.push(`/diagnosis/${this.plant.gr}/${p[0].name}`); break
        default: this.diagnosisCursor += 1; break
      }
    },
    toNextStep (currentStep) {
      if (this.diagnosisCursor > this.steps.length) {
        // Finish Diagnosis Proccess
        this.COMPLETED_STEPS.forEach(step => {
          console.log(step.qualifier, '==>', step.selectedValues)
        })
        swal({
          type: 'warning',
          title: 'Τέλος'
        })
      } else {
        // Procceed to Next Step
        this.diagnosisCursor += 1
        this.STEP_VALUES_MODEL = []
        this.COMPLETED_STEPS.push(currentStep)
      }
    },
    selectValue (step, value) {
      step.selectedValues = this.STEP_VALUES_MODEL
      step.disabled = (step.selectedValues.length === 0)
    },
    resetSymptoms () {
      this.selectedSymptoms = []
    },
    resetSystem () {
      this.diagnosisCursor = 1
      this.COMPLETED_STEPS = []
      this.resetSymptoms()
    }
  }
}
</script>

<style lang="css">
.ntt{
  text-transform: none;
}
.nbr{
  border-radius: 0px;
}
.alert{
  border: none;
}
</style>



