<template>
  <CardLayout icon="local_florist" title="Διάγνωση" url="/diagnosis">
    <template slot="content">
      <!-- <transition-group name="slide-y-transition"> -->
        <template v-if="plants !== null">
          <v-alert key="infoAlert" class="my-3 elevation-1 alert" color="light-green" v-if="routePlantNull" :value="true" type="info" transition="slide-y-transition">
            Πατώντας την Κοινή Ονομασία κάθε κηπευτικού, μεταφέρεστε στον αντίστοιχο σύνδεσμο του
            <b>Wikipedia</b>(αν υπάρχει) ενώ πατώντας κάθε λειτουργία μεταφέρεστε στη σελίδα της
            αντίστοιχης δραστηριότητας. Για περαιτέρω επεξεργασία της βάσης γνώσεως, μεταβείτε στη σελίδα Επεξεργασίας.
          </v-alert>
          <v-alert key="errorAlert" class="my-3 elevation-1 alert" v-else-if="plants !== null && plantNotExist" :value="true" type="error" transition="slide-y-transition">
            Το φυτό <b>{{ $route.params.gr }}</b> δεν βρέθηκε.<br>
            Διορθώστε την διεύθυνση <b>URL</b> και προσπαθήστε ξανά, αλλιώς ακολουθήστε τον Οδηγό Διάγνωσης για κάποιο από τα παρακάτω κηπευτικά.
          </v-alert>
          <transition-group name="slide-x-transition">
            <SelectSymptom key="1" v-if="sickPlantObject !== undefined" :sickPlant="sickPlantObject"></SelectSymptom>
            <PlantsCatalog key="2" v-else :plants="plants"></PlantsCatalog>
          </transition-group>
        </template>
      <!-- </transition-group> -->
      <LoadingPlants class="my-4" v-if="plants == null"></LoadingPlants>
    </template>
  </CardLayout>
</template>

<script>
import API from '@/lib/API'
import SelectSymptom from '@/components/SelectSymptom'
import PlantsCatalog from '@/components/PlantsCatalog'
import LoadingPlants from '@/components/LoadingPlants'
import CardLayout from '@/components/layouts/CardLayout'

export default {
  name: 'Plants',
  components: {
    SelectSymptom,
    PlantsCatalog,
    LoadingPlants,
    CardLayout
  },
  data () {
    return {
      icon: 'local_florist',
      plants: null,
      catalog: true,
      infoAlert: true
    }
  },
  created () {
    API.getPlants().then(plants => {
      this.plants = plants
    }).catch(error => console.log(error))
  },
  mounted () {},
  computed: {
    sickPlantObject: function () {
      return this.plants.filter(plant => plant.gr === this.$route.params.gr)[0]
    },
    routePlantNull () {
      return (this.$route.params.gr === undefined)
    },
    plantNotExist: function () {
      return this.sickPlantObject === undefined
    }
  },
  methods: {}
}
</script>

<style lang="css">
.ntt {
  text-transform: none;
}
.nbr{
  border-radius: 0px;
}
.fwn{
  font-weight: normal;
}
.alert{
  border: none;
}
</style>



